import React from "react";

interface BackgroundProps {
    children?: React.ReactNode;
    backImage: string;
}

let Background = ({ children, backImage }: BackgroundProps) => {
    return (
        <div id="appPar" className="flex flex-col items-center overflow-x-hidden bg-yellow-100 bg-repeat print:contents" style={{ backgroundImage: `url(${backImage})` }}>
            {children}
        </div>
    )
}

export default Background;