import React, { useEffect, useState } from "react";

interface AnimationWrapperProps {
    isVisible?: boolean;
    initialDelay?: number;
    direction?: "left" | "right" | "up" | "down";
    children?: React.ReactNode;
}

let AnimationWrapper = ({ isVisible = false, initialDelay = 0, direction = "left", children }: AnimationWrapperProps) => {
    let tranformInitialValue = "";
    let tranformFinalValue = "";

    switch (direction) {
        case "left":
            tranformInitialValue = `translateX(-120%)`;
            tranformFinalValue = `translateX(0%)`;
            break;
        case "right":
            tranformInitialValue = `translateX(120%)`;
            tranformFinalValue = `translateX(0%)`;
            break;
        case "up":
            tranformInitialValue = `translateY(-120%)`;
            tranformFinalValue = `translateY(0%)`;
            break;
        case "down":
            tranformInitialValue = `translateY(120%)`;
            tranformFinalValue = `translateY(0%)`;
            break;

        default:
            break;
    }

    const [barState, setBarState] = useState(false);

    useEffect(() => {
        setBarState(isVisible);
    }, [isVisible])

    let initialBar = {
        opacity: "0",
        transform: tranformInitialValue,
        scale: "75%",
        transitionProperty: 'opacity, transform, scale',
        transitionDuration: '0.5s',
        transitionDelay: `${initialDelay}s`,
        transitionTimingFunction: 'ease-out',
    }

    let finalBar = {
        opacity: '1',
        transform: tranformFinalValue,
        scale: "100%",
        transitionProperty: 'opacity, transform, scale',
        transitionDuration: '0.5s',
        transitionDelay: `${initialDelay}s`,
        transitionTimingFunction: 'ease-in',
    }

    return (
        <div className="print:contents" style={
            barState ?
                finalBar :
                initialBar
        }>
            {children}
        </div>
    );
}

export default AnimationWrapper;