import React from "react";

interface GridProps {
    children?: React.ReactNode;
}

let Grid = ({ children }: GridProps) => {
    return (
        <div className="grid grid-cols-1 print:grid-cols-12 a4:grid-cols-12 grid-rows-1 min-h-full">
            {children}
        </div>
    )
}

export default Grid;