import React from "react";
import { useRecoilValue } from "recoil";
import { selectLanguange } from "./RecoilState";

export interface ExperienceItemProps {
    title: string,
    startDate?: string | number,
    endDate?: string | number,
    subTitle: string,
    bulletPoints: string[]
    technologies?: string[]
}

let ExperienceItem = ({ title, startDate, endDate, subTitle, bulletPoints, technologies }: ExperienceItemProps) => {

    const lang = useRecoilValue(selectLanguange);

    let parseDate = (date: string | number | undefined) => {
        if (date === null || date === undefined) {
            return null;
        }

        if (typeof date === 'number') {
            return date;
        }

        const d = new Date(date);

        if (isNaN(d.getDate())) {
            return date;
        }

        return d.toLocaleString(lang, { month: 'long', year: 'numeric' });

    }

    let createDate = (startDate: string | number | undefined, endDate: string | number | undefined) => {
        let leftDate = parseDate(startDate);
        let rightDate = parseDate(endDate);

        if (leftDate && rightDate) {
            return (
                <div className="text-gray-800 font-semibold text-sm text-right">
                    {leftDate} - {rightDate}
                </div>
            )
        } else {
            return null;
        }
    }

    return (
        <div className="mt-6">

            <div className="text-gray-800 text-base font-semibold text-left">
                {title}
            </div>
            <div className="flex flex-row justify-between gap-3">
                <div className="text-sm text-left italic">
                    {subTitle}
                </div>
                {createDate(startDate, endDate)}
            </div>
            <div className="mt-2 text-gray-700 text-justify text-sm ml-3 a4:ml-6">
                <ul className="list-disc">
                    {
                        bulletPoints.map((b, i) => (
                            <li key={i}>{b}</li>
                        ))
                    }
                </ul>

                {technologies && technologies?.length !== 0 ?
                    <div className="flex flex-row gap-2 flex-wrap mt-4">
                        {
                            technologies.map((tech, i) =>
                                <span key={i} className="bg-antiquegreen rounded-lg text-center self-center font-mono text-xs" style={{ padding: '2px' }}>{tech}</span>
                            )
                        }
                    </div> :
                    null
                }
            </div>
        </div>
    )
}

export default ExperienceItem;