import React from "react";
import ShowOnScroll from "./ShowOnScroll";

interface DescriptionProps {
    name: string;
    position: string;
    title: string;
    description: string;
}

let Description = ({ name, position, title, description }: DescriptionProps) => {
    let nameSplit = name.split(" ");
    let nameSmall = nameSplit.length >= 2 ? nameSplit[0] + " " + nameSplit[nameSplit.length - 1] : name;
    return (
        <div>
            <ShowOnScroll direction="left">
                <div className="hidden a4:block print:block mt-16 text-left font-mono uppercase text-5xl font-extrabold text-gray-700">{name}</div>
                <div className="mt-4 a4:hidden print:hidden text-center font-mono uppercase text-5xl font-extrabold text-gray-700">{nameSmall}</div>
            </ShowOnScroll>

            <ShowOnScroll direction="right">
                <div className="mt-4 text-center a4:text-right font-mono uppercase text-3xl font-bold text-gray-500 bg-antiquegreen p-1 rounded-xl">{position}</div>
            </ShowOnScroll>

            <ShowOnScroll direction="left">
                <div className="text-gray-900 text-2xl font-semibold mt-8 uppercase">
                    <span className="border-antiquegreen border-b-4">
                        {title}
                    </span>
                </div>
            </ShowOnScroll>

            <ShowOnScroll direction="right">
                <div className="mt-6 text-gray-700 text-sm text-justify">
                    {description}
                </div>
            </ShowOnScroll>
        </div>
    )
}

export default Description;