import React from "react";
import profilePic from "../img/profile_pic.jpg";
import ShowOnScroll from "./ShowOnScroll";

interface ProfilePicProps {
    onlyMobile?: boolean;
}

let ProfilePic = ({ onlyMobile = false }: ProfilePicProps) => {
    return (
        <div className={`${onlyMobile ? "a4:hidden print:hidden" : "a4:block"} bg-gray-300 p-2 rounded-xl`}>
            <ShowOnScroll direction="up">
                <img className={`rounded-full w-40 border-4 border-gray-50 m-auto`} src={profilePic} alt="Profile" />
            </ShowOnScroll>
        </div>
    );
}

export default ProfilePic;