import React from "react";
import type { ExperienceItemProps } from "./ExperienceItem";
import ExperienceItem from "./ExperienceItem";
import ShowOnScroll from "./ShowOnScroll";

export interface ExperienceListProps {
    title?: string;
    data: ExperienceItemProps[];
    start?: number,
    end?: number
}

let ExperienceList = ({ title, data, start, end }: ExperienceListProps) => {
    return (
        <div>

            <ShowOnScroll direction="left">
                <div className="text-gray-900 text-2xl font-semibold mt-8 uppercase">
                    <span className="border-antiquegreen border-b-4">
                        {title}
                    </span>
                </div>
            </ShowOnScroll>

            {
                data.slice(start, end).map((d, i) => (
                    <ShowOnScroll key={i} direction="right">
                        <ExperienceItem  {...d} />
                    </ShowOnScroll>
                ))
            }
        </div >
    )
}

export default ExperienceList;