import React, { useEffect, useState } from "react";

export interface ProgressBarProps {
    name: string;
    percentage: number;
}

let ProgressBar = ({ name, percentage }: ProgressBarProps) => {

    const [barState, setBarState] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setBarState(true);
        }, 1000);
    }, [])

    let initialBar = {
        width: '0%',
    }

    let finalBar = {
        width: `${percentage}%`,
        transitionProperty: 'width',
        transitionDelay: '1s',
        transitionDuration: '0.2s',
        transitionTimingFunction: 'ease-in-out',
    }

    return (
        <div className="transition transform duration-100 group hover:scale-105">
            <div className="text-gray-600 font-sans font-bold text-sm mt-1">{name}</div>
            <div className="bg-gray-100 rounded-lg overflow-hidden h-2 relative">
                <div
                    className="absolute h-2 bg-antiquegreen-darker group-hover:bg-red-400"
                    style={
                        barState ?
                            finalBar :
                            initialBar
                    } />
            </div>
        </div>
    );
}

export default ProgressBar;