import { atom, selector } from "recoil";
import en_EN from '../data/en_EN.json';
import pt_PT from '../data/pt_PT.json';

const languages = [en_EN, pt_PT];

export const selectLanguange = atom({
    key: "selectLanguange",
    default: 'pt-PT'
});

export const currentLanguage = selector({
    key: "currentLanguage",
    get: ({ get }) => {
        let c = get(selectLanguange);

        switch (c) {
            default:
            case 'en-EN':
                return languages[0];
            case 'pt-PT':
                return languages[1];
        }
    }
});
