import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from "@fortawesome/free-regular-svg-icons";
import { faCaretRight, faPhone } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import LeftHeader from "./LeftHeader";
import ShowOnScroll from "./ShowOnScroll";

export interface InfoData {
    name: string;
    value: string;
    type?: string | "git" | "email" | "linkedin" | "phone";
}

interface PersonalInfoProps {
    title: string;
    data: Array<InfoData>
}

let PersonalInfo = ({ title, data }: PersonalInfoProps) => {
    let nameStyles = `text-gray-800 font-sans font-bold text-base group-hover:text-red-500`;
    let valueStyles = `mb-4 font-semibold text-sm text-gray-600 group-hover:text-gray-900 font-sans break-words`;

    let parseValues = (info: InfoData, i: number) => {
        const { name, type, value } = info;
        const rightMargin = 8;

        switch (type) {
            case "email":
                return (
                    <li key={i} className="transition transform duration-100 group hover:scale-105">
                        <div className={nameStyles}>
                            <FontAwesomeIcon style={{ 'marginRight': `${rightMargin}px` }} icon={faEnvelope} fixedWidth />
                            {name}
                        </div>
                        <div className={valueStyles}>
                            <a href={`mailto:${value}`}>{value}</a>
                        </div>
                    </li>
                );
            case "linkedin":
                return (
                    <li key={i} className="transition transform duration-100 group hover:scale-105">
                        <div className={nameStyles}>
                            <FontAwesomeIcon style={{ 'marginRight': `${rightMargin}px` }} icon={faLinkedin} fixedWidth />
                            {name}
                        </div>
                        <div className={valueStyles}>
                            <a href={`http://${value}`}>{value}</a>
                        </div>
                    </li>
                );
            case "phone":
                return (
                    <li key={i} className="transition transform duration-100 group hover:scale-105">
                        <div className={nameStyles}>
                            <FontAwesomeIcon style={{ 'marginRight': `${rightMargin}px` }} icon={faPhone} fixedWidth />
                            {name}
                        </div>
                        <div className={valueStyles}>
                            <a href={`tel:+${value}`}>{value}</a>
                        </div>
                    </li>
                );
            case "git":
                return (
                    <li key={i} className="transition transform duration-100 group hover:scale-105">
                        <div className={nameStyles}>
                            <FontAwesomeIcon style={{ 'marginRight': `${rightMargin}px` }} icon={faGithub} fixedWidth />
                            {name}
                        </div>
                        <div className={valueStyles}>
                            <a href={`http://${value}`}>{value}</a>
                        </div>
                    </li>
                );
            default:
                return (
                    <li key={i} className="transition transform duration-100 group hover:scale-105">
                        <div className={nameStyles}>
                            <FontAwesomeIcon style={{ 'marginRight': `${rightMargin}px` }} icon={faCaretRight} fixedWidth />
                            {name}
                        </div>
                        <div className={valueStyles}>
                            {value}
                        </div>
                    </li>
                );
        }
    }


    return (
        <div>
            <ShowOnScroll direction="left">
                <LeftHeader title={title} />
            </ShowOnScroll>
            <ul className="mt-1 text-left">
                {
                    data.map((info, i) =>
                        <ShowOnScroll key={i} direction="right">
                            {parseValues(info, i)}
                        </ShowOnScroll>

                    )
                }
            </ul>
        </div>
    );
}

export default PersonalInfo;