import React, { useState } from 'react';
import { useRecoilState, useRecoilValue } from 'recoil';
import A4Page from './components/A4Page';
import Background from './components/Background';
import ColumnOne from './components/ColumnOne';
import ColumnTwo from './components/ColumnTwo';
import Description from './components/Description';
import ExperienceList from './components/ExperienceList';
import Footer from './components/Footer';
import Grid from './components/Grid';
import Modal from './components/Modal';
import OptionsContainer from './components/OptionsContainer';
import PersonalInfo from './components/PersonalInfo';
import ProfilePic from './components/ProfilePic';
import { currentLanguage, selectLanguange } from './components/RecoilState';
import Skills from './components/Skills';
import backImage from './img/black-linen.png';


let App = () => {

  const [lang, setLang] = useRecoilState(selectLanguange);
  const [showModal, setShowModal] = useState(false);

  const onLanguageChange = (event: any) => {
    setLang(event.target.value);
  }

  const showAbout = () => {
    window.alert(options[0].value);
  }

  const { personalInfoData, profile, educationData, experienceData, academicProjectsData, skillsData, languages, options } = useRecoilValue(currentLanguage);

  return (
    <Background backImage={backImage}>
      <A4Page pageNumber={1} marginTop={true}>
        <Grid>
          <ColumnOne>
            <ProfilePic />
            <PersonalInfo {...personalInfoData} />
            <Skills {...skillsData} />
          </ColumnOne>

          <ColumnTwo>
            <ProfilePic onlyMobile={true} />
            <Description {...profile} />
            <ExperienceList {...experienceData} />
          </ColumnTwo>
        </Grid>
      </A4Page>


      <A4Page pageNumber={2}>
        <Grid>
          <ColumnOne>
          </ColumnOne>
          <ColumnTwo>
            <ExperienceList {...educationData} />
            <ExperienceList {...academicProjectsData} start={0} end={3} />
          </ColumnTwo>
        </Grid>
      </A4Page>


      <A4Page pageNumber={3}>
        <Grid>
          <ColumnOne>
          </ColumnOne>
          <ColumnTwo>
            <ExperienceList {...academicProjectsData} start={3} />
          </ColumnTwo>
        </Grid>
      </A4Page>

      <Footer>
        <PersonalInfo {...personalInfoData} />
      </Footer>

      <Modal isShow={showModal} setModal={() => setShowModal(false)}>
        <h1 className="mb-4 text-2xl font-mono text-red-400 font-bold capitalize">About this app</h1>
        <p>This app was build with:</p>
        <ul className="list-disc ml-4">
          <li>React</li>
          <li>Recoil</li>
          <li>TypeScript</li>
          <li>Tailwind CSS</li>
        </ul>
      </Modal>
      <OptionsContainer>

        <button className="w-full bg-gray-300 shadow-2xl hover:bg-gray-500 hover:text-gray-50 p-2 rounded-lg text-gray-900 text-base flex flex-col justify-items-center items-center" onClick={() => setShowModal(true)}>
          <span className="self-center">
            {options[0].value}
          </span>
        </button>

        <select className="block w-full py-2 px-3 border rounded-lg shadow-2xl text-base border-none bg-gray-300 hover:bg-gray-500 hover:text-gray-50 text-gray-900"
          value={lang} onChange={onLanguageChange}>
          {
            languages.map(v => (
              <option key={v.abr} value={v.abr}>{v.translation}</option>
            ))
          }
        </select>
      </OptionsContainer>


    </Background>
  );
}

export default App;
