import React, { useEffect, useState } from "react";
import "./Modal.css";

interface ModalProps {
    children?: React.ReactNode;
    isShow: boolean;
    setModal: any;
}

let Modal = ({ isShow, setModal, children }: ModalProps) => {

    const [anim, setAnim] = useState(false);

    useEffect(() => {
        setAnim(isShow);
    }, [isShow])

    return (
        <>
            {
                isShow ?
                    < div className="fixed top-0 left-0 bottom-0 right-0 flex flex-col justify-center items-center z-50" >
                        <div className={`relative w-full h-full bg-gray-600 ${anim ? 'modal-bg-open' : "modal-bg-close"}`}
                            onTransitionEnd={() => {
                                if (!anim) setModal(false)
                            }}>

                        </div>
                        <div className={`absolute m-2 a4:m-0 bg-gray-50 p-5 rounded-lg flex flex-col justify-center items-center shadow-2xl ${anim ? 'modal-open' : "modal-close"}`}>
                            <div className="mb-4">
                                {children}
                            </div>
                            <button className="self-end bg-gray-300 shadow-2xl hover:bg-gray-500 hover:text-gray-50 p-2 rounded-lg text-gray-900 text-base flex flex-col justify-items-center items-center" onClick={() => setAnim(false)}>Close</button>
                        </div>
                    </div >
                    : null
            }
        </>
    );
}

export default Modal;