import React from "react";

interface ColumnTwoProps {
    children?: React.ReactNode;
}

let ColumnTwo = ({ children }: ColumnTwoProps) => {


    return (
        <div className="col-span-1 flex flex-col a4:pl-3 a4:pr-3 a4:col-span-8 print:pl-3 print:pr-3 print:col-span-8 overflow-hidden">
            {children}
        </div>
    )
}

export default ColumnTwo;