import React, { useState } from "react";
import './OptionsContainer.css';

interface OptionsContainerProps {
    children?: React.ReactNode;
}

let OptionsContainer = ({ children }: OptionsContainerProps) => {

    const [optionState, setOptionState] = useState(false);

    let handleState = () => {
        setOptionState(!optionState);
    }

    return (
        <div className={`print:hidden fixed origin-bottom-right bottom-5 right-5 a4:bottom-10 a4:right-10 bg-white shadow-lg p-4 ${optionState ? 'open-state' : ' close-state'} `}>

            <div className={`${optionState ? 'open-state-child ' : 'close-state-child'} flex flex-col gap-4 justify-center items-end`}>
                <button className="origin-top-right top-0 right-0 bg-antiquegreen text-gray-500 hover:bg-gray-400 hover:text-gray-50 p-2 w-8 h-8 flex justify-center items-center rounded-lg shadow-lg cursor-pointer" onClick={handleState}>X</button>
                {children}
            </div>

            <button className={`${!optionState ? 'inverse-open-state-child ' : 'inverse-close-state-child pointer-events-none'} absolute origin-center top-0 left-0 flex flex-col justify-center items-center rounded-full w-12 h-12 bg-antiquegreen text-gray-500 hover:bg-gray-400 hover:text-gray-50 border-2 border-antiquegreen-darker shadow-lg`} onClick={handleState}>
                ...
            </button>


        </div>
    )
}

export default OptionsContainer;