import React, { useLayoutEffect, useRef, useState } from "react";
import { throttle } from "lodash";
import AnimationWrapper from "./AnimationWrapper";

interface ShowOnScrollProps {
    initialDelay?: number;
    direction?: "left" | "right" | "up" | "down";
    children?: React.ReactNode;
}

let ShowOnScroll = ({ initialDelay = 0, direction = "left", children }: ShowOnScrollProps) => {

    const scrollIntervalCallback = 250;
    const [elemState, setElemState] = useState(false);
    const elemRef = useRef<HTMLDivElement>(null);

    useLayoutEffect(() => {
        const updateScrollPos = () => {

            if (elemRef.current) {
                let divPosTop = elemRef.current.getBoundingClientRect().top;
                let divPosBot = elemRef.current.getBoundingClientRect().bottom;

                if ((divPosBot < -25 || divPosTop > (window.innerHeight - 50)) && elemState) {
                    setElemState(false)
                }

                if (divPosTop > 0 && divPosTop < (window.innerHeight - 50) && !elemState) {
                    setElemState(true)
                }
            }
        }

        window.addEventListener("scroll", throttle(updateScrollPos, scrollIntervalCallback, { trailing: true, leading: true }));
        updateScrollPos();
        return () => window.removeEventListener("scroll", throttle(updateScrollPos, scrollIntervalCallback, { trailing: true, leading: true }));
    })


    return (
        <div ref={elemRef}>
            <AnimationWrapper isVisible={elemState} initialDelay={initialDelay} direction={direction}>
                {children}
            </AnimationWrapper>
        </div>
    );
}

export default ShowOnScroll;